<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="index.html" :to="{name:'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="cart.html" :to="{name:'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        {{totalAmount}} {{ declOfNum(totalAmount, ['', '', '']) }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              title=""
              placeholder="   "
              v-model="formData.name"
              :error="formError.name"/>

            <BaseFormText
              title=" "
              placeholder="  "
              v-model="formData.address"
              :error="formError.address"/>

            <BaseFormText
              title=""
              placeholder="  "
              type="tel"
              v-model="formData.phone"
              :error="formError.phone"/>

            <BaseFormText
              title="Email"
              placeholder="  Email"
              type="email"
              v-model="formData.email"
              :error="formError.email"/>

            <BaseFormTextarea
              title="  "
              placeholder=" "
              v-model="formData.comment"
              :error="formError.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only"
                  type="radio"
                  name="delivery"
                  value="0"
                  checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.product.id">
              <h3>{{item.product.title}}</h3>
              <b>{{item.amount}} x {{item.product.price | numberFormat}} </b>
              <span>: {{item.product.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>:
              <b>{{totalAmount}}</b>
              {{ declOfNum(totalAmount, ['', '', '']) }}
               
              <b>{{totalPrice | numberFormat}} </b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="loading">
            <span v-if="loading">  </span>
            <span v-else> </span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import declOfNum from '@/helpers/declOfNum';

import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';

import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      loading: false,
    };
  },
  components: { BaseFormText, BaseFormTextarea },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),
  },
  filters: {
    numberFormat,
  },
  methods: {
    declOfNum,
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.loading = true;

      axios
        .post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((res) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', res.data);
          this.$router.push({ name: 'orderInfo', params: { id: res.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .then(() => {
          this.loading = false;
        });
    },
  },
};
</script>
