<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image.file.url"
        width="120"
        height="120"
        :alt="item.product.name">
    </div>
    <h3 class="product__title">
      {{ item.product.name }}
    </h3>
    <p class="product__info">
      : <span>128GB</span>
    </p>
    <span class="product__code">
      {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
      <button type="button"
        aria-label="  "
        @click.prevent="changeAmount('sub')">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button"
        aria-label="  "
        @click.prevent="changeAmount()">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.amount * item.product.price | numberFormat }} 
    </b>

    <div v-if="deletingProductCart"> </div>

    <button class="product__del button-del"
      type="button"
      aria-label="   "
      @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  props: ['item'],
  data() {
    return {
      deletingProductCart: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.updateAmount({ productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapActions({
      deleteCartProduct: 'deleteCartProduct',
      updateAmount: 'updateCartProductAmount',
    }),
    deleteProduct(productId) {
      this.deletingProductCart = true;
      this.deleteCartProduct(productId)
        .then(() => {
          this.deletingProductCart = false;
        });
    },
    changeAmount(method) {
      let amount = method === 'sub' ? this.item.amount - 1 : this.item.amount + 1;
      if (amount < 1) { amount = 1; }
      this.updateAmount({ productId: this.item.productId, amount });
    },
  },
};
</script>
