<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        {{ pagination.total }} {{ declOfNum(pagination.total, ['', '', '']) }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color-id.sync="filterColorId"
      />

      <section class="catalog">
        <Preloader v-if="productsLoading" />

        <div v-if="productsLoadingFailed">
             
          <button @click.prevent="loadProducts">  </button>
        </div>

        <ProductList
          :products="products"
          :color-id="filterColorId" />

        <BasePagination v-model="page" :pages="pagination.pages" />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import Preloader from '@/components/Preloader.vue';

import axios from 'axios';
import declOfNum from '@/helpers/declOfNum';
import { page, productsPerPage, API_BASE_URL } from '../config';

export default {
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
    Preloader,
  },
  data() {
    return {
      page,
      productsPerPage,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    pagination() {
      return this.productsData ? this.productsData.pagination : 0;
    },
  },
  methods: {
    declOfNum,
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
            },
          })
          .then((res) => {
            this.productsData = res.data;
          })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
